import React, { Component, PropTypes } from 'react'
import { Page, Navbar, List } from 'jmui'

export default class Index extends Component {

  static propTypes = {};

  static contextTypes = {
    router: PropTypes.object
  };

  handleJump (pathname) {
    this.context.router.push(pathname)
  }

  render () {
    return (
      <Page
        fix
        navbar={<Navbar title='Kitchen Sink' />}>
        <List>
          <List.Group title=''>
            <List.Item
              icon={<span className='iconfont icon-anniu' />}
              title=''
              after='Button'
              onClick={this.handleJump.bind(this, '/kitchensink/components/button')}
            />
            <List.Item
              icon={<span className='iconfont icon-biaoqian' />}
              title=''
              after='Icon'
              onClick={this.handleJump.bind(this, '/kitchensink/components/icon')}
            />
            <List.Item
              icon={<span className='iconfont icon-iconfonttupian' />}
              title=''
              after='Image'
              onClick={this.handleJump.bind(this, '/kitchensink/components/image')}
            />
            <List.Item
              icon={<span className='iconfont icon-badge' />}
              title=''
              after='Badge'
              onClick={this.handleJump.bind(this, '/kitchensink/components/badge')}
            />
          </List.Group>
          <List.Group title=''>
            <List.Item
              icon={<span className='iconfont icon-biaodan' />}
              title=''
              after='Form'
              onClick={this.handleJump.bind(this, '/kitchensink/components/form')}
            />
            <List.Item
              icon={<span className='iconfont icon-danxuan' />}
              title=''
              after='RadioField'
              onClick={this.handleJump.bind(this, '/kitchensink/components/radiofield')}
            />
            <List.Item
              icon={<span className='iconfont icon-duoxuan12px' />}
              title=''
              after='CheckField'
              onClick={this.handleJump.bind(this, '/kitchensink/components/checkfield')}
            />
            <List.Item
              icon={<span className='iconfont icon-jiahao' />}
              title=''
              after='NumberField'
              onClick={this.handleJump.bind(this, '/kitchensink/components/numberfield')}
            />
            <List.Item
              icon={<span className='iconfont icon-xuanze' />}
              title=''
              after='Picker'
              onClick={this.handleJump.bind(this, '/kitchensink/components/picker')}
            />
          </List.Group>
          <List.Group title=''>
            <List.Item
              icon={<span className='iconfont icon-daohang' />}
              title=''
              after='Navbar'
              onClick={this.handleJump.bind(this, '/kitchensink/components/navbar')}
            />
            <List.Item
              icon={<span className='iconfont icon-tab' />}
              title=''
              after='Tabbar'
              onClick={this.handleJump.bind(this, '/kitchensink/components/tabbar')}
            />
            <List.Item
              icon={<span className='iconfont icon-dibutiao' />}
              title=''
              after='Toolbar'
              onClick={this.handleJump.bind(this, '/kitchensink/components/toolbar')}
            />
          </List.Group>
          <List.Group title=''>
            <List.Item
              icon={<span className='iconfont icon-list' />}
              title=''
              after='List'
              onClick={this.handleJump.bind(this, '/kitchensink/components/list')}
            />
            <List.Item
              icon={<span className='iconfont icon-iconfonttupian' />}
              title=''
              after='MediaList'
              onClick={this.handleJump.bind(this, '/kitchensink/components/medialist')}
            />
            <List.Item
              icon={<span className='iconfont icon-card' />}
              title=''
              after='Card'
              onClick={this.handleJump.bind(this, '/kitchensink/components/card')}
            />
          </List.Group>
          <List.Group title=''>
            <List.Item
              icon={<span className='iconfont icon-tongzhi' />}
              title=''
              after='Notification'
              onClick={this.handleJump.bind(this, '/kitchensink/components/notification')}
            />
            <List.Item
              icon={<span className='iconfont icon-loader' />}
              title=''
              after='Preloader'
              onClick={this.handleJump.bind(this, '/kitchensink/components/preloader')}
            />
          </List.Group>
          <List.Group title=''>
            <List.Item
              icon={<span className='iconfont icon-fenleizukuaier' />}
              title=''
              after='Block'
              onClick={this.handleJump.bind(this, '/kitchensink/components/block')}
            />
            <List.Item
              icon={<span className='iconfont icon-wanggetu' />}
              title=''
              after='Grid'
              onClick={this.handleJump.bind(this, '/kitchensink/components/grid')}
            />
            <List.Item
              icon={<span className='iconfont icon-folding' />}
              title=''
              after='Accordion'
              onClick={this.handleJump.bind(this, '/kitchensink/components/accordion')}
            />
          </List.Group>
          <List.Group title=''>
            <List.Item
              icon={<span className='iconfont icon-shouji' />}
              title=''
              after='View'
              onClick={this.handleJump.bind(this, '/kitchensink/components/view')}
            />
            <List.Item
              icon={<span className='iconfont icon-pages' />}
              title=''
              after='Pages'
              onClick={this.handleJump.bind(this, '/kitchensink/components/pages')}
            />
            <List.Item
              icon={<span className='iconfont icon-qiehuan' />}
              title=''
              after='TransitionPages'
              onClick={this.handleJump.bind(this, '/kitchensink/components/transitionpages')}
            />
            <List.Item
              icon={<span className='iconfont icon-wenjian' />}
              title=''
              after='Page'
              onClick={this.handleJump.bind(this, '/kitchensink/components/page')}
            />
          </List.Group>
        </List>
      </Page>
    )
  }
}



